}

// Testimonials
.section-testimonials{
	.testimonials-content{
		.icon-quotes{
			font-size: var(--space-xl-3);
		}
		.testimonials-description{
			font-size: var(--heading-5);
			line-height: 32px;
			margin-bottom: var(--space-md);
			@media (max-width: 576px) {
				font-size: var(--body-font-size-lg);
				line-height: 24px;
			}
		}
		.testimonials-author{
			&-name{
				@include flex-center-y;
				font-size: var(--heading-6);
				font-weight: var(--font-weight-semibold);
				margin-bottom: var(--space-xs-4);
				> a{
					@include flex-center-y(inline-flex);
					color: var(--linkedin);
					font-size: var(--heading-6);
					vertical-align: sub;
					margin-left: var(--space-xs-3);
					transition: color .3s ease;
					&:hover{
						color: darken($linkedin, 16%);
						text-decoration: none;
					}
				}
			}
			&-role{
				color: var(--fg-subtlest);
			}
			&-avatar{
				width: var(--space-xl-5);
				height: var(--space-xl-5);
				border: 1px solid var(--border-light);
				border-radius: var(--radius-rounded);
				background-color: var(--bg-neutral);
			}
			&-content{
				@include flex-center-y;
				margin-bottom: var(--space-xl);
				&-left{
					margin-right: var(--space-sm);
				}
			}
		}
	}
}

// Swiper
.swiper-pagination-bullet-active{
	background: var(--bg-brand);
}

@keyframes line-blow-up {
	0%{
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}
	50%{
		top: -12%;
		left: -12%;
		bottom: -12%;
		right: -12%;
	}
	100%{
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}
}

// Contact
.section-contact{
	padding-top: var(--space-xl-4);
	padding-bottom: var(--space-xl-4);
	position: relative;
	@include main-gradient;
	.shape-wave{
		&-1, &-2{
			position: absolute;
			width: 100%;
			left: 0;
			right: 0;
			z-index: 1;
		}
		&-1{
			top: 0;
		}
		&-2{
			bottom: -1px;
		}
	}
	.shape-tree{
		&-1, &-2{
			position: absolute;
			// width: 100%;
			bottom: 0;
			@media (max-width: 991px) {
				display: none;
			}
		}
		&-1{
			left: 0;
			width: 500px;
		}
		&-2{
			right: 0;
		}
	}
	.content-text{
		z-index: 999;
		.title{
			@include text-heading(1);
			color: var(--fg-brand);
			margin-bottom: var(--space-lg);
			@media (max-width: 576px) {
				@include text-heading(2);
			}
			a{
				color: var(--fg-brand);
				text-decoration: underline;
			}
		}
		@media (min-width: 768px) {
			.actions{
				.btn +.btn{
					margin-left: var(--space-xs-2);
				}
			}
		}
		@media (max-width: 414px) {
			.actions{
				.btn{
					width: 100%;
					&:first-child{
						margin-bottom: var(--space-xs-2);
					}
				}
			}
		}
	}
	.content-bg{
		svg{
			margin: 0 auto;
		}
	}
	@media (max-width: 991px) {
		padding-top: 0;
		padding-bottom: 0;
		.content-bg{
			svg{
				width: 100%;
				height: 460px;
			}
		}
	}
	@media (max-width: 768px) {
		.content-bg{
			svg{
				width: 100%;
				height: 430px;
			}
		}
	}
	@media (max-width: 576px) {
		padding-top: var(--section-margin-mobile);
		overflow: hidden;
		.content-text{
			text-align: center;
		}
		.content-bg{
			svg{
				// width: 800px;
				// height: 400px;
				transform: translateY(50px);
			}
		}
	}
}

// Resume
.section-personal-info{
	&-container{
		background-color: var(--surface-1);
		border-radius: var(--radius-xl);
		box-shadow: 0 1px 24px 0px rgba(0,0,0,0.03);
		border: 0.5px solid var(--border-light);
		padding: var(--space-md);
		background-image: radial-gradient(var(--neutral-100) 1.3px, transparent 1px);
		backdrop-filter: saturate(50%) blur(4px);
		background-size: 12px 12px;
		@media (min-width: 992px) {
			padding: var(--space-xl);
		}
	}
	&-header{
		@media (min-width: 992px) {
			display: flex;
		}
		&-left{
			@media (max-width: 991px) {
				margin-bottom: var(--space-md);
			}
			.avatar{
				position: relative;
				width: var(--space-xl-7);
				height: var(--space-xl-7);
				img{
					// max-width: var(--space-xl-7);
					border-radius: var(--radius-rounded);
					background-color: var(--bg-neutral);
					border: 1px solid var(--border-light);
					cursor: pointer;
				}
				@media (min-width: 992px) {
					margin-right: var(--space-md);
				}
				.badge{
					width: max-content;
					position: absolute;
					// bottom: calc(var(--space-xs-3) * -1);
					// left: 50%;
					// transform: translateX(-50%);
					bottom: calc(var(--space-xs-3) + 6px * -1);
    				left: 52%;
					transform: translateX(-50%) rotate(-4deg);
					box-shadow: var(--shadow-300);
					z-index: 1;
					&.open-to-work{
						border: 2px solid transparent;
						animation: border-angle-rotate 2s infinite linear;
						background: linear-gradient(var(--badge-bg-success), white) padding-box,
							conic-gradient(
								from var(--border-angle),
								var(--badge-border-success) 50%,
								white
								)
								border-box;
					}
				}
			}
		}
		&-right{
			.name{
				@media (min-width: 992px) {
					@include text-heading(3);
				}
				@include text-heading(4);
			}
			.role{
				@media (min-width: 992px) {
					@include text-body(lg);
				}
				color: var(--fg-subtlest);
			}
			.description{
				font-size: var(--body-font-size-lg);
				margin-bottom: var(--space-sm);
			}
			.actions{
				.btn:not(:last-child){
					@media (min-width: 576px) {
						margin-right: var(--space-xs-2);
					}
					margin-bottom: var(--space-xs-2);
				}
			}
		}
	}
	.divider{
		margin-top: var(--space-lg);
		margin-bottom: var(--space-lg);
	}
	.group-title{
		@media (min-width: 992px) {
			@include text-heading(3);
		}
		@include text-heading(4);
		margin-bottom: var(--space-md);
	}
	&-body{
		.timeline-content{
			.date-range{
				color: var(--fg-subtlest);
				@media (min-width: 992px) {
					@include text-body(lg);
				}
			}
			.job-title{
				@media (min-width: 992px) {
					@include text-heading(6);
				}
				@include text-body(lg);
				font-weight: var(--font-weight-semibold);
			}
			.company-name{
				font-weight: var(--font-weight-medium);
			}
			ul{
				&.description{
					margin-top: var(--space-xs-2);
					padding-left: var(--space-xl-5);
					li{
						@media (min-width: 992px) {
							@include text-body(lg);
						}
						&:not(:last-child){
							margin-bottom: var(--space-xs-3);
						}
					}
				}
			}
			.milestone{
				&:not(:last-child){
					margin-bottom: var(--space-md);
				}
				&-header{
					@include flex-center-y;
					&-left{
						margin-right: var(--space-xs);
						.logo{
							width: var(--space-xl-2);
							height: var(--space-xl-2);
							border-radius: var(--radius-xs);
							border: .5px solid var(--border-light);
							background-color: var(--bg-neutral);
						}
					}
				}
				&.milestone-start, &.milestone-mid{
					position: relative;
					&::after{
						content: '';
						position: absolute;
						left: var(--space-md);
						top: 56px;
						bottom: 0;
						width: .5px;
						height: calc(100% - 56px + 18px);
						background-color: var(--border-light);
					}
				}
				&.milestone-end, &.milestone-mid{
					.milestone-header{
						&-left{
							.logo{
								background-color: transparent;
								@include flex-center;
								border-width: 0;
								.dot{
									position: relative;
									width: var(--space-md);
									height: var(--space-md);
									border-radius: var(--radius-rounded);
									border: .5px solid var(--border-strong);
									background-color: rgba($neutral-200, .1);
									&::before{
										content: '';
										position: absolute;
										left: var(--space-xs-2);
										right: var(--space-xs-2);
										top: var(--space-xs-2);
										bottom: var(--space-xs-2);
										border-radius: var(--radius-rounded);
										background-color: var(--fg-disabled);
									}
								}
							}
						}
					}
				}
			}
		}
		&-experience{
			position: relative;
			@media (min-width: 768px) {
				&::before{
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					width: .5px;
					height: 100%;
					background-color: var(--border-light);
				}
			}
			@media (max-width: 767px) {
				margin-bottom: var(--space-lg);
				&::before{
					content: '';
					position: absolute;
					left: var(--space-md);
					right: var(--space-md);
					bottom: 0;
					width: calc(100% - var(--space-md) *2);
					height: .5px;
					background-color: var(--border-light);
				}
			}
		}
		.profile-content{
			ul{
				list-style-type: none;
				padding-left: 0;
				margin-bottom: 0;
				li{
					display: flex;
					flex-wrap: wrap;
					.text-wrapper{
						@include flex-center-y;
						@media (min-width: 992px) {
							font-size: var(--body-font-size-lg);
						}
						.icon{
							width: var(--ui-size-sm);
							height: var(--ui-size-sm);
							font-size: 20px;
							margin-right: var(--space-xs-2);
							background-color: var(--bg-neutral);
							border-radius: var(--radius-rounded);
							@include flex-center;
						}
						.value{
							margin-right: var(--space-xs-3);
						}
						.value, &>i{
							line-height: var(--space-md);
						}
					}
					&:not(:last-child){
						margin-bottom: var(--space-sm);
					}
				}
			}
		}
	}
}